@import url(https://fonts.googleapis.com/css?family=Open+Sans);
* {
  margin: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  text-align: center;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.first_card {
  border: solid;
  
}

.header {
  background-color: black;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  background-image: linear-gradient(315deg, black 0%, #1a1b1b 74%);
}

.header_icon {
  display: flex;
  padding: 10px 40px;
  overflow: hidden;
}

.header_img {
  width: 20%;
  height: auto;
  cursor: pointer;
}

/* Hide the scroll bar */

.header::-webkit-scrollbar {
  display: none;
}

.form {
  margin: 0 auto;
  display: inline-block;
  padding: 5px;
  width: 500px;
}

.search {
  margin: 0 auto;
  display: inline-block;
  padding: 5px;
  width: 750px;
  object-fit: contain;
  height: 25x;
  border: solid black 3px;
}

.cards {
  background-image: linear-gradient(#808080, #a9a9a9 40%);
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  grid-template-columns: repeat(3, 32%);
  transition: transform 80ms;
  max-width: 100%;
  width: auto;
}
.card {
  padding: 15px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  grid-template-columns: repeat(3, 32%);
  transition: transform 80ms;
  width: auto;
  object-fit: contain;
  width: 300px;
  height: auto;
  border: solid black;
}

.card_image {
  height: 250px;
  padding: 2px;
  object-fit: contain;
  width: 300px;
}
.card_price {
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  font-size: large;
  padding: 10px;
  justify-content: center;
}

.card_header {
  padding: 20px;
  font-family: Georgia, "Times New Roman", Times, serif;
  font-size: xx-large;
  justify-content: center;
  width: 270px;
}

.card:hover {
  display: inline;
}

.card_image:hover {
  display: inline;
}

